import {distanceInWords} from 'date-fns';
export default function Table({scraps}) {
    const scrapReversed = [...scraps].reverse();

    console.log(scrapReversed)
    return (
       <table>
           <thead>
               <tr>
                   <td>Count</td>
                   <td>Time</td>
               </tr>
           </thead>
           <tbody>
               {scrapReversed.map(scrap => (
                   <tr key={scrap.Date}>
                        <td>{scrap.count}</td>
                        <td>- {distanceInWords(new Date(scrap.Date), new Date())}</td>
                   </tr>
               ))}
           </tbody>
       </table>
    )
}